import axios from "axios" 

const productActions = {

    search : (text) => {
        return async (dispatch, getState) => {
            try {
                const arreglo = await axios.get('https://hardspace-webapp.herokuapp.com/api/products/'+text)

                if (arreglo.data.success && !arreglo.data.error) {

                    dispatch({type: "SEARCH", payload: arreglo.data.response})
                    return arreglo

                } else {
                    
                    console.log('No existen articulos para tu bsqueda.')
                    console.log('dentro del else del action:: este es arreglo.data:: ',arreglo.data)
                    return arreglo

                }

            } catch(error) {
                console.error(error)
            }
        }
    }
}

export default productActions